import { Link as GatsbyLink } from "gatsby";
import { OutboundLink } from "gatsby-plugin-google-analytics";
import React from "react";
export const Link = ({ activeClassName, children, className, partiallyActive, to, }) => {
    const internal = /^\/(?!\/)/.test(to);
    return internal ? (<GatsbyLink to={to} className={className} activeClassName={activeClassName} partiallyActive={partiallyActive}>
      {children}
    </GatsbyLink>) : (<OutboundLink href={to} target="_blank" className={className}>
      {children}
    </OutboundLink>);
};
